.header,
.menu,
.content {
  box-sizing: border-box;
}

.app {
  height: 100%;
}

html,
#root,
body {
  height: 100%;
  margin: 0px;
}

body {
  font-family: Helvetica, Arial;
}
.app-container {
  display: flex;
  width: 100%;
  height: 100%;
}
.app-body {
  display: flex;
  flex: 1;
}

.content {
  width: 100%;
  position: relative;
  flex: 1;
}

.circular {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
